  font-style: normal;
}

body {
  font-family: 'Futura Book', 'Futura Demi', sans-serif;
  font-size: 16px;
  color: #fff;
  background: #fff;
}

input:focus,
input:active,
textarea:focus,
textarea:active,
button:focus,
button:active,
span:focus,
span:active,
button:focus,
button:active {
  outline: none;
}

select:focus,
select:hover,
option:hover,
option:focus {
  outline: none;
}

.green {
  color: #E3B873;
}

.wrapper {
  max-width: 1320px;
  padding: 0 30px;
  margin: 0 auto;
}

.dark-bg {
  background: #131313;
  padding-bottom: 420px;
}

.black {
  color: #181818;
}

.xs-show {
  display: none;
}

.btn {
  font-family: 'Futura Demi', sans-serif;
  font-size: 13px;
  color: #fff;
  text-transform: uppercase;
  width: 161px;
  padding: 13px 0;
  background: #E3B873;
  box-shadow: 0 5px 15px rgba(227, 184, 115, 0.2);
  border-radius: 2px;
  border: none;
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.btn:hover {
  transform: scale(1.05);
}

.btn.bigger {
  padding: 19px 0;
  width: 207px;
}

.btn.non-bg {
  background: inherit;
  border: 1px solid #fff;
  box-shadow: none;
}

h2 {
  font-family: 'Yeseva One', sans-serif;
  font-size: 27px;
  text-transform: uppercase;
  color: #E3B873;
  letter-spacing: -0.07em;
  line-height: 1.4;
}

h2.after-line {
  position: relative;
}

h2.after-line:after {
  content: '';
  width: 92px;
  height: 1px;
  background: #E3B873;
  position: absolute;
  bottom: 18px;
  left: 160px;
}

h3 {
  font-family: 'Yeseva One', sans-serif;
  font-size: 20px;
  text-align: center;
  text-transform: uppercase;
}

.section-img {
  display: table;
  margin: 12px auto 31px;
  position: relative;
}

.section-img:before {
  content: '';
  width: 32px;
  height: 1.5px;
  background: #E3B873;
  position: absolute;
  left: -35px;
  top: calc((100% - 1.5px) / 2);
}

.section-img:after {
  content: '';
  width: 32px;
  height: 1.5px;
  background: #E3B873;
  position: absolute;
  right: -35px;
  top: calc((100% - 1.5px) / 2);
}

.tab {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  max-width: 1340px;
  margin: 0 auto;
}

.tab__item {
  font-family: 'Yeseva One', sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.3);
  margin-right: 60px;
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.tab__item:hover {
  color: #E3B873;
}

.tab__item_active {
  position: relative;
  color: #E3B873;
}

.tab__item_active:before {
  content: '';
  width: 100%;
  height: 1px;
  background: #E3B873;
  position: absolute;
  bottom: -6px;
}

.slider-arrow {
  display: flex;
  align-items: center;
  margin-top: 80px;
}

.slider-arrow__item {
  cursor: pointer;
}

.slider-arrow__item:first-child {
  margin-right: 30px;
}

.slider-arrow__item:last-child {
  margin-left: 30px;
}

.pagination {
  display: flex;
  align-items: center;
}

.pagination__item {
  width: 10px;
  height: 10px;
  margin: 0 9px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.pagination__item:hover {
  background: #fff;
}

.pagination__item_active {
  background: #fff;
}

label {
  font-family: 'Futura Book', sans-serif;
  font-size: 15px;
  color: rgba(54, 54, 54, 0.5);
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.name-input {
  text-align: left;
}

.input-styles {
  font-family: 'Futura Book', sans-serif;
  font-size: 18px;
  color: #363636;
  border: none;
  border-bottom: 1px solid rgba(54, 54, 54, 0.3);
  background: inherit;
  padding: 8px 0;
  width: 100%;
}

.input-styles::placeholder {
  color: #363636;
}

.input-styles::-webkit-outer-spin-button, .input-styles::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.wrap-checkbox {
  margin-top: 40px;
  display: inline-block;
}

.checkbox {
  display: none;
}

.checkbox:checked + .style-checkbox:before {
  color: #fff;
  background: #E3B873;
}

.style-checkbox {
  display: inline-block;
  position: relative;
  font-family: 'Futura Book', sans-serif;
  font-size: 16px;
  padding-left: 30px;
  color: #181818;
}

.style-checkbox:before {
  content: "\2713";
  color: transparent;
  display: inline-block;
  border: 1px solid #E3B873;
  font-size: 12px;
  line-height: 19px;
  margin-top: -1px;
  height: 16px;
  width: 16px;
  text-align: center;
  vertical-align: middle;
  transition: color ease .3s;
  position: absolute;
  left: 0;
}

