.person__icon {
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #E3B873;
  box-shadow: 0 5px 20px rgba(227, 184, 115, 0.5);
  position: absolute;
  left: -35px;
  top: -35px;
}

.person__name {
  padding: 0 10px;
  font-family: 'Yeseva One', sans-serif;
  color: #181818;
  font-size: 18px;
}

.person__position {
  padding: 0 10px;
  font-size: 18px;
  margin-top: 9px;
  color: #959595;
}

.person__experience {
  padding: 13px 10px;
  margin-top: 23px;
  font-family: 'Yeseva One', sans-serif;
  font-size: 13px;
  text-transform: uppercase;
  background: #E3B873;
}

.person .description-person {
  position: relative;
  box-sizing: border-box;
  margin: -80px auto 0;
  width: 100%;
  max-width: 233px;
  background: #fff;
  text-align: center;
  padding-top: 20px;
}

.our-client {
  margin-top: 90px;
}

.our-client .clients {
  display: flex;
  justify-content: space-between;
}

.description-our-client {
  font-size: 20px;
  color: #363636;
  max-width: 400px;
  margin: 0 auto 39px;
  line-height: 1.3;
  text-align: center;
}

.client {
  background: #fff;
  box-shadow: 0 10px 50px rgba(0, 0, 0, 0.04);
  padding: 20px 0 85px;
  flex-basis: 28.5%;
}

.client .head {
  display: flex;
  align-items: center;
  padding: 0 30px 0 20px;
}

.client__name {
  font-family: 'Yeseva One', sans-serif;
  font-size: 18px;
  line-height: 1.3;
  padding-left: 20px;
  color: #131313;
}

.client__img img {
  border-radius: 50%;
}

.client__comment {
  padding: 0 30px 0 20px;
  margin-top: 18px;
  font-size: 15px;
  color: #959595;
  line-height: 1.6;
}

.client .footer-card {
  height: 54px;
  margin-top: 60px;
  margin-bottom: -110px;
  box-shadow: 5px 5px 20px rgba(227, 184, 115, 0.5);
  background: #E3B873;
  display: flex;
  width: 80%;
}

.client__size, .client__days {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 11px 5px;
  font-size: 17px;
  font-family: 'Yeseva One', sans-serif;
  text-transform: uppercase;
}

.client__size img, .client__days img {
  padding-right: 15px;
}

.client__size {
  width: 45%;
}

.client__days {
  width: 55%;
  border-left: 1px solid #fff;
}

.contacts {
  margin-top: 160px;
}

.contacts .dark {
  padding-top: 275px;
  background: linear-gradient(180deg, rgba(24, 24, 24, 0.9) 3.47%, rgba(24, 24, 24, 0.9) 100%);
}

.contacts .bg-contacts {
  background: url(../assets/images/bg-contacts.jpg) no-repeat 100% 0;
  -webkit-background-size: cover;
  background-size: cover;
}

.contacts .wrap-form {
  background: #131313;
  max-width: 883px;
  margin: 0 auto -222px;
  position: relative;
}

.contacts .form-feedback {
  max-width: 520px;
  margin: 0 auto;
  padding: 34px 15px;
}

.contacts .form-feedback label {
  margin-top: 45px;
}

.contacts .form-feedback .name-form {
  font-family: 'Yeseva One', sans-serif;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
}

.contacts .form-feedback .row {
  display: flex;
  justify-content: space-between;
}

.contacts .form-feedback .row label {
  max-width: 230px;
}

.contacts .form-feedback .btn {
  display: block;
  margin: 50px auto 0;
}

.contacts .form-feedback .wrap-checkbox {
  display: table;
  margin: 0 auto;
}

.contacts .form-feedback .style-checkbox {
  color: #fff;
}

.contacts .input-styles {
  color: #fff;
}

.contacts .input-styles::placeholder {
  color: #fff;
}

.contacts .name-input {
  color: rgba(255, 255, 255, 0.5);
}

.location {
  font-family: 'Yeseva One', sans-serif;
  color: #E3B873;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
  max-width: 590px;
  margin: 52px auto 0;
  line-height: 1.3;
}

.map {
  width: 100%;
  max-width: 925px;
  margin: 44px auto 0;
  padding-bottom: 65px;
}

.map iframe {
  height: 470px;
  width: 100%;
}

.social-network {
  display: flex;
  justify-content: center;
  margin-top: 15px;
}

.social-network__item {
  margin: 0 7.5px;
}

@media only screen and (max-width: 1200px) {
  nav {
    max-width: 400px;
  }
  .price .wrap {
    margin: -25px auto 0;
  }
  .steps .ellipse svg {
    height: 507px;
  }
  .steps .row {
    align-items: flex-start;
    padding-top: 47px;
  }
  .steps .left-side {
    margin-left: 245px;
  }
  .steps .steps-list {
    min-width: 375px;
  }
  .steps .steps-list__item p {
    font-size: 16px;
  }
  .steps .steps-list__item:nth-child(2) {
    margin-top: 43px;
    margin-left: 63px;
  }
  .steps .steps-list__item:nth-child(3) {
    margin-top: 87px;
    margin-left: 82px;
  }
  .steps .steps-list__item:nth-child(4) {
    margin-top: 86px;
    margin-left: 29px;
  }
  .client {
    flex-basis: 32%;
  }
}

@media only screen and (max-width: 992px) {
  .md-hide {
    display: none;
  }
  .prices .row, .projects .row {
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .price {
    flex-basis: 45%;
    margin-top: 10px;
  }
  .price:nth-child(2) {
    margin: 10px 0 0;
  }
  .price .wrap {
    margin: -126px auto 0;
  }
  .steps {
    background: url(../assets/images/bg-steps.png) no-repeat 0 0;
  }
  .steps .row {
    flex-direction: column;
  }
  .steps .right-side {
    max-width: inherit;
  }
  .catalog {
    margin-top: 100px;
  }
  .catalog .content {
    align-items: center;
  }
  .goals .btn-slides {
    flex-basis: 33.33%;
  }
  .team {
    margin-top: 0;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .person {
    margin-top: 110px;
  }
  .our-client {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .wrapper {
    padding: 0 15px;
  }
  .sm-show {
    display: flex;
  }
  .sm-hide {
    display: none;
  }
  .shadow-bg {
    padding: 85px 0 61px;
  }
  h2 {
    text-align: center;
  }
  h2.after-line:after {
    display: none;
  }
  .box {
    flex-direction: column;
    padding: 0 15px;
  }
  .feature {
    margin-right: 0;
  }
  .description-project {
    text-align: center;
    max-width: inherit;
    padding: 0 17px;
  }
  .feature-list {
    max-width: inherit;
  }
  .slider + .feature-list {
    display: flex;
  }
  .slider-img {
    margin-top: 36px;
  }
  .slider-img__item {
    width: 100vw;
  }
  .slider-img__item img {
    width: 100vw;
  }
  .slider-img-arrow__item_next {
    right: 15px;
  }
  .slider-img-arrow__item_prev {
    left: 15px;
    display: flex;
  }
  .video-s {
    height: 307px;
  }
  .control .input-styles {
    max-width: inherit;
  }
  .control .form-feedback {
    margin-top: 40px;
  }
  .control .form-feedback .btn {
    margin-top: 60px;
    width: 100%;
  }
  .control .form-feedback .row {
    flex-direction: column;
  }
  .control .form-feedback .row label:nth-child(n + 2) {
    margin-top: 60px;
  }
  .control .form-feedback .row .wrap-checkbox {
    margin-top: 28px;
    display: block;
  }
  .control .form-feedback .row .wrap-checkbox .style-checkbox {
    margin-top: 0;
  }
  .prices .row, .projects .row {
    flex-direction: column;
    align-items: center;
  }
  .price:nth-child(n + 2) {
    margin-top: 59px;
  }
  .price__img img {
    max-width: 440px;
  }
  .steps {
    display: none;
  }
  .catalog {
    background: inherit;
    margin-top: 55px;
    margin-bottom: -144px;
  }
  .catalog .catalog-list {
    padding: 0;
    margin: 0 auto;
  }
  .catalog .catalog-representation {
    display: none;
  }
  .catalog .list-style {
    display: none;
  }
  .catalog .slider {
    display: block;
    margin-top: 30px;
  }
  .catalog .slider .slide {
    text-align: center;
  }
  .catalog .slider .slide img {
    width: 100%;
    max-width: 400px;
  }
  .projects {
    margin-bottom: 0;
    margin-top: 0;
    padding-top: 190px;
    background: #131313;
  }
  .projects h3 {
    color: #fff;
  }
  .description-projects {
    color: #fff;
    line-height: 1.3;
  }
  .goals {
    display: none;
  }
  .our-team {
    margin-top: 0;
    padding-top: 76px;
    background: #131313;
  }
  .more-description-person {
    color: #fff;
  }
  .contacts {
    margin-top: 0;
    padding-top: 0;
  }
}

@media only screen and (max-width: 576px) {
  .xs-hide {
    display: none;
  }
  .xs-show {
    display: block;
  }
  .logo {
    font-size: 12px;
  }
  .dark-bg {
    padding-bottom: 0;
  }
  .header .tel {
    font-size: 12px;
    color: #fff;
  }
  .header .tel:before {
    display: none;
  }
  .title-banner {
    font-size: 23px;
  }
  h1 {
    font-size: 23px;
  }
  h2 {
    font-size: 22px;
  }
  .description-page {
    margin-top: 16px;
    font-size: 18px;
  }
  .banner .buttons {
    margin-top: 32px;
    flex-direction: column;
    max-width: 100%;
  }
  .banner .buttons .btn {
    width: 100%;
  }
  .banner .buttons .btn:first-child {
    margin-bottom: 26px;
  }
  .description-project {
    margin-top: 22px;
    font-size: 14px;
  }
  .slider-img-arrow {
    top: calc((100% - 36px) / 2);
  }
  .slider-img-arrow__item {
    width: 36px;
    height: 36px;
  }
  .slider-img-arrow__item img {
    height: 11px;
  }
  .feature-list__name {
    font-size: 14px;
  }
  .feature-list__signification {
    font-size: 14px;
  }
  .control {
    background: #131313;
    margin-top: 0;
    padding-top: 57px;
  }
  .control .input-styles {
    color: #fff;
  }
  .control .input-styles::placeholder {
    color: #fff;
  }
  .control .name-input {
    color: rgba(255, 255, 255, 0.5);
  }
  .control .style-checkbox {
    color: #fff;
  }
  .control .form-feedback .btn {
    margin-bottom: -26px;
  }
  .description-control {
    color: #fff;
  }
  .video-s {
    height: 207px;
  }
  .description-control {
    font-size: 14px;
    padding-top: 21px;
    margin-top: 28px;
  }
  .prices {
    margin-top: 80px;
  }
  .price .wrap {
    margin-top: -160px;
    margin-left: 0;
  }
  .price__name {
    font-size: 15px;
  }
  .price__btn .btn {
    font-size: 15px;
  }
  .price__list {
    max-width: 270px;
  }
  .price .price-description__item {
    font-size: 13px;
  }
  .projects .price__img img {
    filter: brightness(1);
  }
  .more-description-person {
    display: none;
  }
  .contacts {
    background: #131313;
    padding-top: 76px;
  }
  .contacts .wrap-form {
    margin: 0 auto;
  }
  .contacts .dark {
    padding-top: 0;
    background: inherit;
  }
  .contacts .bg-contacts {
    background: inherit;
  }
  .contacts .form-feedback .row {
    flex-direction: column;
  }
  .contacts .form-feedback .row label {
    max-width: inherit;
  }
  .contacts .form-feedback .btn {
    margin-top: 67px;
    width: 100%;
  }
  .contacts .form-feedback .wrap-checkbox {
    margin: 0;
  }
  .contacts .form-feedback .wrap-checkbox label {
    margin-top: 22px;
  }
  .map {
    padding-bottom: 0;
  }
  .social-network {
    padding-bottom: 38px;
  }
  .social-network__item img {
    height: 39px;
  }
  .map iframe {
    height: 300px;
  }
}
